import './App.css';
import React,{useState} from 'react';
import Navbar from './components/Navbar';
import TextForm from './components/TextForm';
import Alert from './components/Alert';
import About from './components/About';
import {
  BrowserRouter as Router,
  Route,
  Link
} from "react-router-dom";
import { Switch } from 'react-router-dom/cjs/react-router-dom.min';


function App() {
  const[mode,setMode] = useState('light'); //Wheater dark mode is enabled or not
  const[alert, setAlert] = useState(null);

  const showAlert = (message, type) =>{
      setAlert({
        message: message,
         type: type
      })
      setTimeout(() =>{
        setAlert(null)
      },1500);
  }
  
  // const changegreen = ()=>{
  //   document.body.style.backgroundColor = '#35C649'
  // }
  // const changeyellow = ()=>{
  //   document.body.style.backgroundColor = '#CAB911'
  // }
  // const changered = ()=>{
  //   document.body.style.backgroundColor = '#C15031'
  // }



  const toggleMode = ()=>{
    if(mode === 'light'){
      setMode('dark');
      document.body.style.background = '#042743';
      showAlert("Dark mode has been Active", "success");
      document.title='Text utils - Dark Mode';
      setInterval(() =>{
        document.title='Text utils is amazing';
      },2000);
      setInterval(() =>{
        document.title='Text utils is install now';
      },1500);
    }else{
      setMode('light');
      document.body.style.background = 'white';
      showAlert("Light mode has been Active", "success");
      document.title='Text utils - Light Mode';
      setInterval(() =>{
        document.title='Text utils is amazing';
      },2000);
      setInterval(() =>{
        document.title='Text utils is install now';
      },1500);
    }
    
  }

  return (
    <>
        {/* <Navbar title="New Webite" aboutText="About us" mode={mode}/> 
        <Navbar title="New Webite" mode={mode} toggleMode={toggleMode}  reacchangegreen={changegreen} changered={changered} changeyellow={changeyellow} /> */}
        <Router>
        <Navbar title="New Webite" mode={mode} toggleMode={toggleMode}/>
        <Alert  alert={alert}/>
        <div className="container my-3"> 
            
            <Switch>
              <Route exact path="/about">
                  <About mode={mode}/>  
              </Route>
              <Route exact path="/">
                  <TextForm showAlert={showAlert} heading="Enter the text to analyze below" mode={mode}/> 
              </Route>
            </Switch>
        
        </div>
        </Router>
     </>
  ); 
}

export default App;
