import React,{useState} from 'react';

export default function About (props) {
    
  return(
     <>
    <div className="container" style={{background: props.mode==='light'?'white':'#042743', color: props.mode==='light'?'black':'white' }}> 
    <h1 className="my-1" style={{background: props.mode==='light'?'white':'#042743', color: props.mode==='light'?'black':'white' }}>About us</h1>
    
    <div className="accordion my-4" id="accordionExample" >
        <div className="accordion-item">
            <h2 className="accordion-header" >
            <button className="accordion-button" type="button"  data-bs-toggle="collapse" data-bs-target="#collapseOne" aria-expanded="false" aria-controls="collapseOne" style={{background: props.mode==='light'?'white':'#437197', color: props.mode==='light'?'black':'white' }}>
                Accordion Item #1
            </button>
            </h2>
            <div id="collapseOne" className="accordion-collapse collapse show"  data-bs-parent="#accordionExample" style={{background: props.mode==='light'?'white':'#042743', color: props.mode==='light'?'black':'white' }}>
            <div className="accordion-body" >
                <strong>This is the first item's accordion body.</strong> It is shown by default, until the collapse plugin adds the appropriate class that we use to style each element. These class control the overall appearance, as well as the showing and hiding via CSS transitions. You can modify any of this with custom CSS or overriding our default variables. It's also worth noting that just about any HTML can go within the <code>.accordion-body</code>, though the transition does limit overflow.
            </div>
            </div>
        </div>
        <div className="accordion-item">
            <h2 className="accordion-header">
            <button className="accordion-button collapsed" type="button"  data-bs-toggle="collapse" data-bs-target="#collapseTwo" aria-expanded="false" aria-controls="collapseTwo" style={{background: props.mode==='light'?'white':'#437197', color: props.mode==='light'?'black':'white' }}>
                Accordion Item #2
            </button>
            </h2>
            <div id="collapseTwo" className="accordion-collapse collapse" data-bs-parent="#accordionExample" style={{background: props.mode==='light'?'white':'#042743', color: props.mode==='light'?'black':'white' }}>
            <div className="accordion-body" >
                <strong>This is the second item's accordion body.</strong> It is hidden by default, until the collapse plugin adds the appropriate class that we use to style each element. These class control the overall appearance, as well as the showing and hiding via CSS transitions. You can modify any of this with custom CSS or overriding our default variables. It's also worth noting that just about any HTML can go within the <code>.accordion-body</code>, though the transition does limit overflow.
            </div>
            </div>
        </div>
        <div className="accordion-item">
            <h2 className="accordion-header">
            <button className="accordion-button collapsed" type="button"  data-bs-toggle="collapse" data-bs-target="#collapseThree" aria-expanded="false" aria-controls="collapseThree" style={{background: props.mode==='light'?'white':'#437197', color: props.mode==='light'?'black':'white' }}>
                Accordion Item #3
            </button>
            </h2>
            <div id="collapseThree" className="accordion-collapse collapse" data-bs-parent="#accordionExample" style={{background: props.mode==='light'?'white':'#042743', color: props.mode==='light'?'black':'white' }}>
            <div className="accordion-body" >
                <strong>This is the third item's accordion body.</strong> It is hidden by default, until the collapse plugin adds the appropriate class that we use to style each element. These class control the overall appearance, as well as the showing and hiding via CSS transitions. You can modify any of this with custom CSS or overriding our default variables. It's also worth noting that just about any HTML can go within the <code>.accordion-body</code>, though the transition does limit overflow.
            </div>
            </div>
        </div>
    </div>
    </div>
    </>
  )
}