import React, {useState} from 'react'

export default function TextForm(props) {
    // const [mytextStyle,setMyStyle] = useState({
    //     width: '230px',
    //     height: '70px'
    // })
    
    const handleupClick = () =>{
        console.log("Uppercase is Clicked"  + text);
        let newText = text.toUpperCase();
        setText(newText)
        props.showAlert("Converted into Uppercase", "warning" );
    }
    const handleloClick = () =>{
        console.log("Lowercase is Clicked"  + text);
        let newText = text.toLowerCase();
        setText(newText)
        props.showAlert("Converted into Lowercase", "warning" );
    }
    const handlecapClick = () =>{
        console.log("Captization is Clicked"  + text);
        let newText = text[0].toUpperCase() + text.slice(1).toLowerCase();
        setText(newText)
        props.showAlert("Converted into First letter Captial", "warning" );
    }
    const handleclearClick = () =>{
        let newText = '';
        setText(newText);
        props.showAlert("Clear Text", "danger" );
    }
    const handleonchange = (event) =>{
        console.log("on changed");
        setText(event.target.value);
    }
    const handleCopy = () => {
        // console.log("I am copy");
        var text = document.getElementById("mybox2");
        text.select();
        navigator.clipboard.writeText(text.value);
        props.showAlert("Copy Text", "primary" );
    }
    const handleExtraSpaces = () =>{
        let newText = text.split(/[ ]+/);
        setText(newText.join(" "))
        props.showAlert("Extra space remove From sentance", "primary" );
    }

    const [text, setText] = useState('Enter text here');
    
    return (
        <>
        <div className="container">
            <h3 className='text-center text-uppercase' style={{background: props.mode==='light'?'white':'#042743', color: props.mode==='light'?'black':'white' }} >{props.heading}</h3>
               <div className="mb-3">
                  <textarea className="form-control" onChange={handleonchange} style={{background: props.mode==='light'?'white':'#042743', color: props.mode==='light'?'black':'white' }} value={text} id="mybox2" rows="8"></textarea>
               </div>
            <button className="btn mx-2 my-2" style={{background: props.mode==='light'?'blue':'white', color: props.mode==='light'?'white':'black' }}  onClick={handleupClick}>Convert into Uppercase</button>
            <button className="btn mx-2 my-2" style={{background: props.mode==='light'?'blue':'white', color: props.mode==='light'?'white':'black' }}   onClick={handleloClick}>Convert into Lowercase</button>
            <button className="btn mx-2 my-2" style={{background: props.mode==='light'?'blue':'white', color: props.mode==='light'?'white':'black' }}   onClick={handlecapClick}>Convert into Capitalization</button>
            <button className="btn mx-2 my-2" style={{background: props.mode==='light'?'blue':'white', color: props.mode==='light'?'white':'black' }}   onClick={handleCopy}>Copy text</button>
            <button className="btn mx-2 my-2" style={{background: props.mode==='light'?'blue':'white', color: props.mode==='light'?'white':'black' }}   onClick={handleExtraSpaces}>Remove Extra Space</button>
            <button className="btn mx-2 my-2" style={{background: props.mode==='light'?'blue':'white', color: props.mode==='light'?'white':'black' }}   onClick={handleclearClick}>Clear Text</button>

            {/* <button className="btn mx-2 my-2" style={{background: props.mode==='light'?'black':'white', color: props.mode==='light'?'white':'black' }} >Convert text in Bold</button>
            <button className="btn mx-2 my-2" style={{background: props.mode==='light'?'black':'white', color: props.mode==='light'?'white':'black' }} >Convert text in underline</button>
            <button className="btn mx-2 my-2" style={{background: props.mode==='light'?'black':'white', color: props.mode==='light'?'white':'black' }} >Convert text in Italic</button>
            <button className="btn mx-2 my-2" style={{background: props.mode==='light'?'black':'white', color: props.mode==='light'?'white':'black' }} >Change Alternating Case</button>
            <button className="btn mx-2 my-2" style={{background: props.mode==='light'?'black':'white', color: props.mode==='light'?'white':'black' }} >Enable Light Mode</button>
            <button className="btn mx-2 my-2" style={{background: props.mode==='light'?'black':'white', color: props.mode==='light'?'white':'black' }} >Change the First letter to Uppercase of word</button>
            <button className="btn mx-2 my-2" style={{background: props.mode==='light'?'black':'white', color: props.mode==='light'?'white':'black' }} >Reverse the text</button>
            <button className="btn mx-2 my-2" style={{background: props.mode==='light'?'black':'white', color: props.mode==='light'?'white':'black' }} >Change Font size in Increment </button>
            <button className="btn mx-2 my-2" style={{background: props.mode==='light'?'black':'white', color: props.mode==='light'?'white':'black' }} >Change Font size in Decrement </button> */}

        </div>
        <div className="container my-5">
            <h4 style={{background: props.mode==='light'?'white':'#042743', color: props.mode==='light'?'black':'white' }} >Your text Summary</h4>
            <p style={{background: props.mode==='light'?'white':'#042743', color: props.mode==='light'?'black':'white' }} >{text.split(" ").length} Words and {text.length} Characters</p>
            <p style={{background: props.mode==='light'?'white':'#042743', color: props.mode==='light'?'black':'white' }} >{0.008 * text.split(" ").length} Minutes</p>
            <h4 style={{background: props.mode==='light'?'white':'#042743', color: props.mode==='light'?'black':'white' }} >Preview</h4>
            <p style={{background: props.mode==='light'?'white':'#042743', color: props.mode==='light'?'black':'white' }} >{text.length>0?text:"Enter Something to preview it here"}</p>
            <p>{text.trim() === '' ? 0 : text.match(/\S+/g).length} words and {text.replace(/\s+/g, '').length} characters</p>
        </div>
        </>
    );
}